var html =[];
function list() {
  for (var i = 0; i <= projects.length; i++) {
    console.log(projects[i], i);
    var item = <li><a href={projects[i].url} target="_blank">{projects[i].title}</a></li>
    html.push(item);
}
return html;
};

export default () => (
  <ul>
    {list()}
  </ul>
);